import React from 'react'

import P1 from './P1'
import P2 from './P2'
import P3 from './P3'
import P4 from './P4'
import P5 from './P5'

export default function Routes (props) {
  const [slider, setSlider] = React.useState()

  React.useEffect(() => {
    const slider = new window.PageSlider({
      pages: window.$('.page-wrap .page'),
      onchange: function () {
        const page = document.querySelector('.page.current').id
        switch (page) {
          case 'page1':
            document.title = '李泽昊和吕唯的婚礼请柬'
            break;
          case 'page2':
            document.title = '请柬（1/4）'
            break;
          case 'page3':
            document.title = '确认出席（2/4）'
            break;
          case 'page4':
            document.title = '选择伴手礼（3/4）'
            break;
          case 'page5':
            document.title = '填写完成（4/4）'
            break;
        }
      },
    })
    setSlider(slider)
  }, [])

  return (
    <div className='page-wrap'>
      <P1 />
      <P2 />
      <P3 nextPage={() => slider.next()} />
      <P4 nextPage={() => slider.next()} />
      <P5 />
    </div>
  )
}
