import React, { PureComponent, lazy } from 'react'

import redux, { Provider } from '@obsidians/redux'

import { config, updateStore } from '@/redux'
import api from '@/api'

import LoadingScreen from '~/common/LoadingScreen'

const Pages = lazy(() => import('./Pages' /* webpackChunkName: "components" */))

export default class ReduxApp extends PureComponent {
  state = {
    loaded: false,
  }

  async componentDidMount () {
    await redux.init(config, updateStore).then(onReduxLoaded)
    await new Promise(resolve => setInterval(resolve, 1000))
    this.setState({ loaded: true })
  }

  render () {
    if (!this.state.loaded) {
      return <LoadingScreen />
    }

    return (
      <Provider store={redux.store}>
        <Pages />
      </Provider>
    )
  }
}

async function onReduxLoaded (state) {
  redux.dispatch('SET_VERSION', { version: '0.1.0' })
  const user = await api.get('user', { id: 'xxx' })
  redux.dispatch('SET_USER', user)
}
