import Immutable, { List, Map } from 'immutable'

export const version = {
  default: Map({}),
  persist: true,
  actions: {
    SET_VERSION: {
      reducer: (state, { payload }) => state.merge(payload)
    }
  }
}

export const user = {
  default: Map({}),
  persist: true,
  actions: {
    SET_USER: {
      reducer: (_, { payload }) => Map(payload)
    }
  }
}