import qs from 'qs'
import redux from '@obsidians/redux'

class HttpClient {
  constructor (serverUrl) {
    this.apiBaseUrl = `${serverUrl}/api/v1`
  }

  async get (apiPath, params) {
    return this.request(`${this.apiBaseUrl}/${apiPath}`, 'GET', params)
  }

  async post (apiPath, params) {
    return this.request(`${this.apiBaseUrl}/${apiPath}`, 'POST', params)
  }

  async put (apiPath, params) {
    return this.request(`${this.apiBaseUrl}/${apiPath}`, 'PUT', params)
  }

  async delete (apiPath) {
    return this.request(`${this.apiBaseUrl}/${apiPath}`, 'DELETE')
  }

  loggedIn () {
    return !!redux.getState().user.get('token')
  }

  async request (endpoint, method, params) {
    const headers = {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    }
    const token = redux.getState().user.get('token')
    if (token) {
      headers.Authorization = `Bearer ${token}`
    }
    const opts = { headers, method }
    if (method === 'POST' || method === 'PUT' || method === 'DELETE') {
      if (params instanceof FormData) {
        opts.body = params
        delete headers['Content-Type']
      } else {
        opts.body = JSON.stringify(params)
      }
    } else if (method === 'GET' && params) {
      endpoint += `?` + qs.stringify(params)
    }

    const response = await fetch(endpoint, opts)

    let result = await response.text()
    try {
      result = JSON.parse(result)
    } catch (e) {}

    if (response.status === 401) {
      redux.dispatch('USER_LOGOUT')
      throw new Error(result.message || 'Need login to perform this operation.')
    } else if (response.status >= 400) {
      throw new Error(result.message || result)
    }
    return result
  }
}

export default new HttpClient(process.env.REACT_APP_SERVER_URL)