import React from 'react'
import classnames from 'classnames'

import bg4 from './images/bg4.png'

import op1 from './images/op1.png'
import op2 from './images/op2.png'
import op3 from './images/op3.png'
import op4 from './images/op4.png'

import check1 from './images/check1.png'
import check2 from './images/check2.png'
import check3 from './images/check3.png'
import check4 from './images/check4.png'

export default function (props) {
  const [active, setActive] = React.useState()

  const onConfirm = () => {
    props.nextPage()
  }

  return (
    <div id='page4' className='page' data-lock-next='true'>
      <div className='long-page'>
        <div style={{ backgroundImage: `url(${bg4})` }} className='page-bg-image'>
          <div className='page4-content'>
            <div
              style={{ backgroundImage: `url(${op1})` }}
              className={classnames('op', active === 'op1' && 'op-active')}
              onClick={() => { setActive('op1') }}
            >
              <div style={{ backgroundImage: `url(${check1})` }} className='check' />
            </div>
            <div
              style={{ backgroundImage: `url(${op2})` }}
              className={classnames('op', active === 'op2' && 'op-active')}
              onClick={() => { setActive('op2') }}
            >
              <div style={{ backgroundImage: `url(${check2})` }} className='check' />
            </div>
            <div
              style={{ backgroundImage: `url(${op3})` }}
              className={classnames('op', active === 'op3' && 'op-active')}
              onClick={() => { setActive('op3') }}
            >
              <div style={{ backgroundImage: `url(${check3})` }} className='check' />
            </div>
            <div
              style={{ backgroundImage: `url(${op4})` }}
              className={classnames('op', active === 'op4' && 'op-active')}
              onClick={() => { setActive('op4') }}
            >
              <div style={{ backgroundImage: `url(${check4})` }} className='check' />
            </div>

            <div style={{ marginTop: '18.5vw' }}>
              <textarea
                className='textarea'
                placeholder=''
              />
            </div>
            <div style={{ marginTop: '6vw', marginBottom: '5vw' }}>
              <div className='confirm' onClick={onConfirm}>确认</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}