import React from 'react'
import classnames from 'classnames'

import bg3 from './images/bg3.png'
import button from './images/button.png'
import buttonActive from './images/button-active.png'

export default function (props) {
  const [active, setActive] = React.useState()
  const input = React.useRef()

  const onConfirm = () => {
    props.nextPage()
  }

  return (
    <div id='page3' className='page' data-lock-next='true'>
      <div className='long-page'>
        <div style={{ backgroundImage: `url(${bg3})` }} className='page-bg-image'>
          <div className='page3-content'>
            <div className='button-group'>
              <div
                style={{ backgroundImage: `url(${active === 'attend' ? buttonActive : button})` }}
                className={classnames('button', active === 'attend' && 'button-active')}
                onClick={() => { setActive('attend'); input.current.focus() }}
              >
                确认出席
              </div>
              <div
                style={{ backgroundImage: `url(${active === 'not-sure' ? buttonActive : button})` }}
                className={classnames('button', active === 'not-sure' && 'button-active')}
                onClick={() => { setActive('not-sure'); input.current.focus() }}
              >
                暂不确定
              </div>
              <div
                style={{ backgroundImage: `url(${active === 'no' ? buttonActive : button})` }}
                className={classnames('button', active === 'no' && 'button-active')}
                onClick={() => { setActive('no'); input.current.focus() }}
              >
                无法参加/<br />线上参加
              </div>
            </div>
            <input ref={input} type='tel' className='input' placeholder='联系方式（手机号）' />
            <div style={{ marginTop: '45vw', marginBottom: '5vw' }}>
              <div className='confirm' onClick={onConfirm}>确认</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}